import { BrowserModule } from '@angular/platform-browser';
import { NgModule, DoBootstrap, Injector } from '@angular/core';
import { createCustomElement } from '@angular/elements';
import { ElementZoneStrategyFactory } from 'elements-zone-strategy';

import { AppComponent } from './app.component';
import { AuthComponent } from './auth/auth.component';

@NgModule({
  declarations: [
    AuthComponent
  ],
  imports: [
    BrowserModule
  ],
  providers: [],
  entryComponents: [AuthComponent]
})
export class AppModule implements DoBootstrap{ 
  constructor(private injector: Injector) {
    const strategyFactory = new ElementZoneStrategyFactory(AuthComponent, injector);
    const webComponent = createCustomElement(AuthComponent, {injector, strategyFactory});
    if(!!customElements.get('auth-pr')) customElements.define('auth-pr', webComponent);
  }

  ngDoBootstrap() {}
}
